<template>
  <div :id="id"></div>
</template>
<script>
import G2 from '@antv/g2'
export default {
  name: 'bar',
  props: {
    id: String
  },
  data() {
    return {
      chart1: null,
      data: [{
        genre: 'Sports',
        sold: 275
      },
      {
        genre: 'Strategy',
        sold: 115
      },
      {
        genre: 'Action',
        sold: 120
      },
      {
        genre: 'Shooter',
        sold: 350
      },
      {
        genre: 'Other',
        sold: 150
      }] // G2  JSON  JSON 
    }
  },
  computed: {
    isCollapse() {
      return this.$store.state.sys.isCollapse
    }
  },
  watch: {
    'isCollapse': {
      handler() {
        setTimeout(() => {
          this.chart1.forceFit()
        }, 400)
      }
    }
  },
  methods: {
    _chart1() {
      this.chart1 = new G2.Chart({
        container: this.id, //  ID
        forceFit: true,
        padding: [20, 20, 95, 40], // 
        height: 350 // 
      })
      // Step 2: 
      this.chart1.source(this.data)
      // Step 3 genre  sold genre  x sold  y 
      this.chart1.interval().position('genre*sold').color('genre')
      // Step 4: 
      this.chart1.render()
    }
  },
  mounted() {
    this.$nextTick(() => {
      this._chart1()
    })
  }
}
</script>



