<template>
  <div :id="id"></div>
</template>
<script>
import data from './bubble.json'
import G2 from '@antv/g2'
export default {
  props: {
    id: String
  },
  data() {
    return {
      chart: null,
      data: data
    }
  },
  computed: {
    isCollapse() {
      return this.$store.state.sys.isCollapse
    }
  },
  watch: {
    'isCollapse': {
      handler() {
        setTimeout(() => {
          this.chart.forceFit()
        }, 400)
      }
    }
  },
  methods: {
    _chart() {
      const { Global } = G2
      const colorMap = {
        'Asia': Global.colors[0],
        'Americas': Global.colors[1],
        'Europe': Global.colors[2],
        'Oceania': Global.colors[3]
      }
      this.chart = new G2.Chart({
        container: this.id,
        forceFit: true,
        height: 350
      })
      this.chart.source(this.data)
      // 
      this.chart.scale({
        LifeExpectancy: {
          alias: ''
        },
        Population: {
          type: 'pow',
          alias: ''
        },
        GDP: {
          alias: '($)'
        },
        Country: {
          alias: '/'
        }
      })
      this.chart.axis('GDP', {
        label: {
          formatter: function(value) {
            return (value / 1000).toFixed(0) + 'k'
          } // 
        }
      })
      this.chart.tooltip({
        showTitle: false // 
      })
      this.chart.legend('Population', false) //  Population  Country 
      this.chart.point().position('GDP*LifeExpectancy')
        .size('Population', [4, 65])
        .color('continent', val => {
          return colorMap[val]
        })
        .shape('circle')
        .tooltip('Country*Population*GDP*LifeExpectancy')
        .style('continent', {
          lineWidth: 1,
          strokeOpacity: 1,
          fillOpacity: 0.3,
          opacity: 0.65,
          stroke: val => {
            return colorMap[val]
          }
        })
      this.chart.render()
    }
  },
  mounted() {
    this.$nextTick(() => {
      this._chart()
    })
  }
}
</script>



